    getPois(latitude,longitude) {
      const location = latitude+","+longitude;
      console.log("location..",location);
      let data = {
        location: location,
        key: qqMapKey,
        get_poi: 1,
        poi_options: 'radius=2000;page_size=10;page_index=1;policy=2'
      };
      console.log("data..",data)
      data.output="jsonp";
      $.ajax({
          type:"get",
          dataType:'jsonp',
          data:data,
          jsonp:"callback",
          jsonpCallback:"QQmap",
          url:qqMapUrl,
          success: mapData=> {
            console.log("mapData..",mapData);
            qqMapCurrentCity = mapData.result.address_component.city;
            this.getNearbyList(mapData.result.pois);
          },
          error : err=> {
            this.alertMsg("");
          }
      });
