    showMapContainer(latitude,longitude) {
      // this.geocoderLocation();
      // console.log("nearbyAddress",this.nearbyAddress);
      $("#allmap").css("height",(window.innerHeight*(7.5/window.innerWidth)-4.2)+"rem");
      let geocoder,map,marker = null;
      let newCenter = new qq.maps.LatLng(latitude, longitude);
      map = new qq.maps.Map(document.getElementById('allmap'),{
          center: newCenter,
          zoom: 15,
          disableDefaultUI: true,
      });
      marker = new qq.maps.Marker({
          position: newCenter,
          draggable: false,
          map: map
      });
      qq.maps.event.addListener(map, 'center_changed', ()=> {
          marker.setPosition(map.getCenter())
          this.getPois(map.getCenter().lat,map.getCenter().lng)
      });
    },
