    checkPhone(value) {
      if (value==="") {
        return true;
      }else{
        var myReg=/^[1][3,4,5,6,7,8,9][0-9]{9}$/;//
        var telReg=/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$///
        if (!myReg.test(value)&&!telReg.test(value)) {
           return false;
        }else{
           return true;
        }
      }
    },
    getQueryString(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) {
            return decodeURI(r[2])
        }
        return null
    }
  }
